import React from 'react'
import $ from 'jquery';
import '../styles/components/timeline.css';
import {Container} from 'react-bootstrap';

<script src="https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js"></script>




export const Timeline = () => {


    var viewport, divWidth, tb;
    $(function() {

        viewport = $('#container').innerWidth();
        tb = $('#thumbs');
        divWidth = tb.outerWidth();

        $('#container').mousemove(function(e)
        {
        tb.css({left: ((viewport - divWidth)*((e.pageX / viewport).toFixed(3))).toFixed(1) +"px" });
         });

        $('.history-block').on('click', function(){
        $('.history-block').css('width', '300px');
        $('.history-block').find('.title').css('width', '260px');
         $('.history-block .timeline').hide(300);
        $(this).css('width', '600px');
        $(this).find('.title').css('width', '500px');
        $('#container').mousemove(function(e)
        {
          tb.css({left: ((viewport - divWidth-300)*((e.pageX / viewport).toFixed(3))).toFixed(1) + 300 + "px" });
        });
        });


        });
        //mobile device
    $(function(){
        if ($(window).width() <= 420) {  

            viewport = $('#container').innerWidth();
            tb = $('#thumbs');
            divWidth = tb.outerWidth();
    
            $('#container').mousemove(function(e)
            {
            tb.css({left: ((viewport - divWidth)*((e.pageX / viewport).toFixed(3))).toFixed(1) +"px" });
             });
    
            $('.history-block').on('click', function(){
            $('.history-block').css('width', '150px');
            $('.history-block').find('.title').css('width', '50px');

            $(this).css('width', '200px');
            $(this).find('.title').css('width', '150px');
            $('#container').mousemove(function(e)
            {
              tb.css({left: ((viewport - divWidth-300)*((e.pageX / viewport).toFixed(3))).toFixed(1) + 300 + "px" });
            });
            });

     } 
    })

        // var background2016 = {
        //     "background": "url(/assets/images/timeline/2016.jpg)",
        //     "background-size":"cover"
        // }
        // var background2017 = {
        //     "background": "url(/assets/images/timeline/2017.jpg)",
        //     "background-size":"cover"
        // }
        // var background2018 = {
        //     "background": "url(/assets/images/timeline/2018.jpg)",
        //     "background-size":"cover"
        // }
        // var background2019 = {
        //     "background": "url(/assets/images/timeline/2019.jpg)",
        //     "background-size":"cover"
        // }
        // var background2020 = {
        //     "background": "url(/assets/images/timeline/2020.jpg)",
        //     "background-size":"cover"
        // }
        // var backgroundFuture = {
        //     "background": "url(https://i.dailymail.co.uk/i/pix/2012/07/23/article-0-142DF918000005DC-387_468x665.jpg)",
        //     "background-size":"cover"
        // }
    return (
        <Container className ="TimeLine" fluid >
            <div id="history">
                <p className="line-1 anim-typewriter">Nuestra Historia</p>
                <div id="container">
                    <div id="thumbs">
                    {/* style = {background2016}
                    style = {background2018}
                    style = {background2019}
                    style = {background2020}
                    style = {background2017}
                    style = {backgroundFuture} */}
                        <div class="history-block b-2016" >
                            <div class="cover"></div>
                            <div class="year">2016</div>
                            <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>
                        </div>
                        <div class="history-block b-2017" >
                        <div class="cover"></div>
                        <div class="year">2017</div>
                        <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>

                        </div>
                        <div class="history-block b-2018" >
                        <div class="cover"></div>
                        <div class="year">2018</div>
                        <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>
                        </div>
                        <div class="history-block b-2019" >
                        <div class="cover"></div>
                        <div class="year">2019</div>
                        <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>
                        </div>
                        <div class="history-block b-2020" >
                        <div class="cover"></div>
                        <div class="year">2020</div>
                        <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>
                        </div>

                        <div class="history-block b-futuro" >
                        <div class="cover"></div>
                        <div class="year">FUTURO</div>
                        <div class="title">Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam</div>
                        </div>
                    </div>
                </div>

            </div>

        </Container>

    )
}
