import React from 'react'
import {CardDeck, Card, Button, Container} from 'react-bootstrap'
import '../styles/components/display-card.css'
export const DisplayCards = () => {
    return (
        <Container className ="display-cards" fluid>
            
            <CardDeck >
                <Card className="card-usm">
                <Card.Body>
                    <Card.Title>¿Ya eres Miembro?</Card.Title>
                    <Card.Text>
                    Si ya tienes cuenta USM o tienes una cuenta a FabLab, ingresa por acá!

                    </Card.Text>
                    <Button className="card-boton">Acceder con Pasaporte USM o cuenta FabLab</Button>
                </Card.Body>
                </Card>
                <Card  className="card-externa">
                <Card.Body>
                    <Card.Title>¿Nuevo por acá?</Card.Title>
                    <Card.Text>
                    Cónoce más sobre FabLab UTFSM por acá
                    </Card.Text>
                    <Button className="card-boton">Conócenos</Button>
                </Card.Body>
                </Card>
            </CardDeck>
        </Container>
    )
}
