import React from 'react';
import '../styles/components/NavbarFablab.css'
import {Navbar, Nav} from 'react-bootstrap'



export const NavbarFablab = () => {

	return (
        <container flex={true}>
        <header id='home'>                    
            <Navbar className='navMain' expand="lg" fixed="top" variant="dark">
                <Navbar.Brand href="#home"><img src='./assets/images/logo-fablab.png' alt='' width='30%'/></Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav" className="justify-content-end">
                    <Nav  activeKey="/home" >
                        <Nav.Link href="#home" >Mision y Vision</Nav.Link>
                        <Nav.Link href="#history">Nuestra Historia</Nav.Link>
                        <Nav.Link href="#team">Equipo</Nav.Link>
                        <Nav.Link href="#team">Infraestructura</Nav.Link>
                        <Nav.Link href="#visit us">Visitanos</Nav.Link>
                        <Nav.Link href="#contactanos">Contáctanos</Nav.Link>      
                    </Nav>
                </Navbar.Collapse>    
            </Navbar>        
        </header>
        </container>
    )
}
