import React from 'react';
import '../styles/components/Footer.css';
import {Container, Row, Col} from 'react-bootstrap';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faHeart,faMapMarkerAlt,faAngleUp} from '@fortawesome/free-solid-svg-icons';
import {faInstagram, faFacebookSquare, faLinkedin, faYoutube, faTwitterSquare} from '@fortawesome/free-brands-svg-icons';

export const Footer = () => {
    return (
        <Container fluid={true}>
            <Row className="footer">    
                <Col>
                    <a className="rrss-logo" href="https://www.instagram.com/fablab_utfsm/"><FontAwesomeIcon icon={faInstagram} color="gray"></FontAwesomeIcon></a>                                      
                    <a className="rrss-logo" href="https://www.facebook.com/fablabusm"><FontAwesomeIcon icon={faFacebookSquare} color="gray"></FontAwesomeIcon></a>                                      
                    <a className="rrss-logo" href="https://www.linkedin.com/company/fablab-utfsm/"><FontAwesomeIcon icon={faLinkedin} color="gray"></FontAwesomeIcon></a>
                </Col>

                <Col xs={6}>
                    <div id="go-top">
                        <a title="Back to Top" href="#home"><FontAwesomeIcon className="up-arrow" icon={faAngleUp}></FontAwesomeIcon></a>
                    </div>

                    <p className="ubicacion"><FontAwesomeIcon  icon={faMapMarkerAlt} color="gray"></FontAwesomeIcon> Av. Vicuña Mackenna 3939, San Joaquín, Santiago</p>                  
                    <p className="love">Creado con <FontAwesomeIcon icon={faHeart}></FontAwesomeIcon> por FabLab UTFSM</p>
                </Col>

                <Col>
                    <a className="rrss-logo" href="https://www.youtube.com/channel/UCUGoYrG_0erOkSNy1VgMZgA"><FontAwesomeIcon icon={faYoutube} color="gray"></FontAwesomeIcon></a>
                    <a className="rrss-logo" href="https://twitter.com/fablab_utfsm"><FontAwesomeIcon icon={faTwitterSquare} color="gray"></FontAwesomeIcon></a>                         
                </Col>      
            </Row>
        </Container>
   );
}
